import React from 'react';
import OpenedSvg from '../images/opened';
import ClosedSvg from '../images/closed';
import config from '../../../config';
import Link from '../link';

const TreeNode = ({ className = '', setCollapsed, collapsed, url, title, items, ...rest }) => {
  console.log('collapsed!!! : ', collapsed)
  console.log('url!!! : ', url)
  console.log('collapsed[url]!!! : ', collapsed[url])

  const isCollapsed = collapsed[url]; // boolean
  // console.log('items!!! : ', items)
  const collapse = () => {
    setCollapsed(url);
  };

  const hasChildren = items.length !== 0;
  // console.log('items.length: ', items.length, + ' ;; ' + items)
  let location;

  if (typeof document != 'undefined') {
    location = document.location;
  }
  const active = // LNB에서 선택되면 true 
    location && (location.pathname === url || location.pathname === config.gatsby.pathPrefix + url);

  const calculatedClassName = `${className} item ${active ? 'active' : ''}`;

  return (
    <li className={calculatedClassName}>
      {title ? ( // markdown의 title
        hasChildren ? (
          <Link to='javascript:void(0)' onClick={collapse}>
            {title}
          </Link>
        ) : (
          <Link to={url}>
            {title}
          </Link>
        ) 
      ) : null }
      {/* 애초에 1 depth에서도 이미 collapse 된거임 이걸 한번에 세팅하는게 아니고 닫아놓으려면 컨피그에서 받는 디폴트머시기값을 잘 바꿔서 처리해야함 */}
      {/* 원래는 collasepd nav를 설정해야지 안나왔음. 이걸 그대로 코드 베낀다음에, 모든 url에 강제로 설정되도록 해야함!! */}
      {!isCollapsed && hasChildren ? (
        <ul>
          {/* {console.log('WHAT?!')}
          {console.log('hasChildren!! !! ', hasChildren)}
          {console.log('isCollapsed!! !! ', isCollapsed)}
          {console.log('THEFUCK?!')} */}
          {items.map((item, index) => (
            <TreeNode
              key={item.url + index.toString()}
              setCollapsed={setCollapsed}
              collapsed={collapsed}
              {...item}
            />
          ))}
        </ul>
      ) : null}
    </li>
  );
};

export default TreeNode;
